export default {
	data() {
		return {
			list: [{
					title: '评论 7',
					componentName: "comment"
				},
				{
					title: '点赞 258',
					componentName: "star"
				}
			],
			activeTab: 0,
		}
	},
	methods: {
		onTabCLick(payload) {
			const {
				index
			} = payload
			this.activeTab = index
		},
		onChange(payload) {
			const {
				index
			} = payload
			this.activeTab = index
		}
	}
}
